// Mobile first
@custom-media --gte-sm (width >= env(--sm));
@custom-media --gte-md (width >= env(--md));
@custom-media --gte-lg (width >= env(--lg));
@custom-media --gte-xl (width >= env(--xl));

// Desktop first
@custom-media --lt-xl (width < env(--xl));
@custom-media --lt-lg (width < env(--lg));
@custom-media --lt-md (width < env(--md));
@custom-media --lt-sm (width < env(--sm));

// Grid
$grid-breakpoints: (
	xs: env(--xs),
	sm: env(--sm),
	md: env(--md),
	lg: env(--lg),
	xl: env(--xl)
);

$container-max-widths: (
	lg: env(--lg)
);

$grid-gutter-width: 2rem;
@function env($variable) {
	@error "\"env()\" function was not provided by PostCSS or dollar-variable was used as passed argument";
}

@function media($query...) {
	@error "\"media()\" function was not provided by PostCSS or was called without arguments";
}
@mixin clearfix {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
}
%reset {
	all: initial;

	// Properties dependent of browser
	quotes: "“" "”" "‘" "’";
	font-family: var(--content-font-family);
	text-align: left;
	color: var(--content-color);
	outline-color: var(--content-color);

	&:focus-visible {
		outline: 1px dotted currentColor;
		outline: 5px auto -webkit-focus-ring-color;
	}

	// Overwrite some properties
	display: block;
	box-sizing: border-box;
}

@mixin reset {
	@extend %reset;
}
@mixin retina {
	// stylelint-disable-next-line media-feature-name-no-vendor-prefix
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		@content;
	}
}
@mixin triangle($direction, $width, $height, $color) {
	$top-width: 0;
	$top-color: rgba(0, 0, 0, 0);
	$right-width: 0;
	$right-color: rgba(0, 0, 0, 0);
	$bottom-width: 0;
	$bottom-color: rgba(0, 0, 0, 0);
	$left-width: 0;
	$left-color: rgba(0, 0, 0, 0);

	@if $direction == (to top) {
		$left-width: $width / 2;
		$right-width: $width / 2;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to top right) or $direction == (to right top) {
		$right-width: $width;
		$right-color: $color;
		$bottom-width: $height;
	} @else if $direction == (to right) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to bottom right) or $direction == (to right bottom) {
		$left-width: $width;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to bottom) {
		$top-width: $height;
		$top-color: $color;
		$left-width: $width / 2;
		$right-width: $width / 2;
	} @else if $direction == (to bottom left) or $direction == (to left bottom) {
		$top-width: $height;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to left) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$right-width: $width;
		$right-color: $color;
	} @else if $direction == (to top left) or $direction == (to left top) {
		$top-width: $height;
		$top-color: $color;
		$right-width: $width;
	} @else {
		$direction: false;

		@warn "Incorrect triangle direction: #{inspect($direction)}";
	}

	@if $direction {
		border-style: solid;
		border-width: $top-width $right-width $bottom-width $left-width;
		border-color: $top-color $right-color $bottom-color $left-color;
		width: 0;
		height: 0;
	}
}
@mixin visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}























































































































































.start-sect {
	@include reset;

	position: relative;
	background-color: var(--content-color);
}

.container-fluid {
	&--lt-xl-fix {
		@media (--lt-xl) {
			max-width: env(--lg);
		}
	}
}

.slides-counter {
	position: absolute;
	top: calc(3.5rem + 20vh);
	display: flex;
	font-weight: 300;
	font-size: 18px;
	line-height: 1.25;
	color: white;
	user-select: none;

	@media (--lt-xl) {
		top: 20vh;
	}

	@media (--lt-md) {
		font-size: 16px;
	}

	&__current {
		transform: translateY(0.22em);

		&::after {
			content: "/";
		}
	}

	&__total {
		font-size: 34px / 18px * 1em;
	}
}

.slides-nav {
	position: absolute;
	bottom: 2rem;
	user-select: none;
	pointer-events: none;

	&__items {
		margin: -1rem;
		display: flex;
		justify-content: flex-end;

		@media (--lt-md) {
			margin: -0.5rem;
		}
	}

	&__item {
		padding: 1rem;
		display: flex;
		flex-flow: column;
		font-weight: 300;
		font-size: 14px;
		line-height: 1.25;
		color: white;
		cursor: pointer;
		pointer-events: auto;

		@media (--lt-md) {
			padding: 0.5rem;
			font-size: 12px;
		}

		&::after {
			content: "";
			margin-top: auto;
			display: block;
			width: 0;
			height: 1px;
			background-color: currentColor;
		}

		&--current {
			&::after {
				width: 26px;
				transition: width 0.5s ease-in-out;
			}
		}

		&-num {
			font-size: 18px / 14px * 1em;
			margin-bottom: 0.5rem;
		}

		&-title {
			margin-bottom: 0.75rem;
			overflow: hidden;
			max-height: 2 * 1.25em;
		}
	}
}

.slides {
	height: 100vh;
}

.slide-item {
	position: relative;
	padding: 3.5rem 0;
	height: 100vh;
	background: none no-repeat 50% 50%/cover;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(2, 2, 2, 0.43);
	}

	&__container {
		position: relative;
		padding-top: 20vh;
		height: 100%;
	}

	&__title {
		@include reset;

		overflow: hidden;
		margin-bottom: 0.5em;
		max-height: 3 * 1.25em;
		font-weight: 500;
		font-size: 72px / 1080px * 100vh;
		line-height: 1.25;
		color: white;
	}

	&__url {
		@include reset;

		display: inline;
		font-size: 14px;
		line-height: 1.25;
		color: white;
		transition: color 0.15s ease-in-out;
		cursor: pointer;
	}
}
