// Mobile first

// Desktop first

// Grid
$grid-breakpoints: (
	xs: 0,
	sm: 640px,
	md: 960px,
	lg: 1280px,
	xl: 1600px
);

$container-max-widths: (
	lg: 1280px
);

$grid-gutter-width: 2rem;
@function env($variable) {
	@error "\"env()\" function was not provided by PostCSS or dollar-variable was used as passed argument";
}

@function media($query...) {
	@error "\"media()\" function was not provided by PostCSS or was called without arguments";
}
@mixin clearfix {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
}
%reset {
	all: initial;

	// Properties dependent of browser
	quotes: "“" "”" "‘" "’";
	font-family: var(--content-font-family);
	text-align: left;
	color: var(--content-color);
	outline-color: var(--content-color);

	&:focus-visible {
		outline: 1px dotted currentColor;
		outline: 5px auto -webkit-focus-ring-color;
	}

	// Overwrite some properties
	display: block;
	box-sizing: border-box;
}

@mixin reset {
	@extend %reset;
}
@mixin retina {
	// stylelint-disable-next-line media-feature-name-no-vendor-prefix
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		@content;
	}
}
@mixin triangle($direction, $width, $height, $color) {
	$top-width: 0;
	$top-color: rgba(0, 0, 0, 0);
	$right-width: 0;
	$right-color: rgba(0, 0, 0, 0);
	$bottom-width: 0;
	$bottom-color: rgba(0, 0, 0, 0);
	$left-width: 0;
	$left-color: rgba(0, 0, 0, 0);

	@if $direction == (to top) {
		$left-width: $width / 2;
		$right-width: $width / 2;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to top right) or $direction == (to right top) {
		$right-width: $width;
		$right-color: $color;
		$bottom-width: $height;
	} @else if $direction == (to right) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to bottom right) or $direction == (to right bottom) {
		$left-width: $width;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to bottom) {
		$top-width: $height;
		$top-color: $color;
		$left-width: $width / 2;
		$right-width: $width / 2;
	} @else if $direction == (to bottom left) or $direction == (to left bottom) {
		$top-width: $height;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to left) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$right-width: $width;
		$right-color: $color;
	} @else if $direction == (to top left) or $direction == (to left top) {
		$top-width: $height;
		$top-color: $color;
		$right-width: $width;
	} @else {
		$direction: false;

		@warn "Incorrect triangle direction: #{inspect($direction)}";
	}

	@if $direction {
		border-style: solid;
		border-width: $top-width $right-width $bottom-width $left-width;
		border-color: $top-color $right-color $bottom-color $left-color;
		width: 0;
		height: 0;
	}
}
@mixin visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}






































































.fast-links {
	@include reset;

	position: relative;
	display: flex;
	overflow: hidden;
	min-height: 100vh;
	background-color: var(--content-color);

	@media (orientation: portrait) {
		flex-direction: column;
		min-height: 0;
	}

	&:hover {
		.item {
			.item__bg {
				@media (orientation: landscape) {
					opacity: 0;
				}
			}

			+ .bg {
				@media (orientation: landscape) {
					transition: opacity 0.5s ease-in-out;
				}
			}

			&:hover {
				.item__bg {
					@media (orientation: landscape) {
						transition: opacity 0s 0.5s;
					}
				}

				+ .bg {
					@media (orientation: landscape) {
						z-index: 2;
						opacity: 1;
					}
				}
			}

			&:not(:hover) {
				.item__content {
					opacity: 0.5;
				}
			}
		}
	}
}

.item {
	position: relative;
	z-index: 3;
	overflow: hidden;
	width: percentage(1 / 3);

	@media (orientation: portrait) {
		width: 100%;
	}

	&__bg {
		position: absolute;
		left: 0;
		top: 0;
		width: 100vw;
		height: 100%;
		background: none no-repeat 50% 50%/cover;
		transition: opacity 0.5s ease-in-out;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(26, 26, 26, 0.5);
		}

		&--2 {
			left: -100%;

			@media (orientation: portrait) {
				left: 0;
			}
		}

		&--3 {
			left: -200%;

			@media (orientation: portrait) {
				left: 0;
			}
		}
	}

	&__content {
		position: relative;
		display: flex;
		flex-flow: column;
		justify-content: center;
		padding: 1rem;
		height: 100%;
		transition: opacity 0.5s ease-in-out;

		@media (orientation: portrait) {
			min-height: 1 / 3 * 100vh;
		}

		&-title {
			@include reset;

			font-weight: var(--content-headings-font-weight);
			font-size: var(--content-h1-font-size);
			line-height: var(--content-headings-line-height);
			text-align: center;
			color: var(--page-background-color);
		}

		&-desc {
			@include reset;

			font-weight: 300;
			font-size: calc(var(--content-font-size) * 24 / 16);
			line-height: 1.25;
			text-align: center;
			color: var(--page-background-color);
		}

		&-link {
			@include reset;

			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			cursor: pointer;
		}

		&--2,
		&--3 {
			border-left: 1px solid rgba(white, 0.2);

			@media (orientation: portrait) {
				border-left: 0;
				border-top: 1px solid rgba(white, 0.2);
			}
		}
	}
}

.bg {
	position: absolute;
	left: 0;
	top: 0;
	z-index: 1;
	width: 100vw;
	height: 100%;
	background: none no-repeat 50% 50%/cover;
	opacity: 0;
	transition: opacity 0s 0.5s;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(26, 26, 26, 0.5);
	}
}
